* {
  margin: 0;
  padding: 0;
}

html {
  -webkit-user-select: none;
  user-select: none;
}

body {
  height: 100%;
  width: 100%;
  background-color: #faf8f0;
  font-family: Varela Round, sans-serif;
  font-size: 16px;
}

h1 {
  color: #776e65;
  font-size: 64px;
}

.container {
  width: 600px;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  display: flex;
}

.title {
  justify-content: space-between;
  margin-bottom: 20px;
  display: flex;
}

.display-banner {
  float: right;
  display: flex;
}

.display-scores {
  text-align: center;
  width: 50px;
  height: 40px;
  text-transform: uppercase;
  color: #f5f5f5;
  background-color: #bbada0;
  border-radius: 3px;
  margin-left: 20px;
  padding: 20px;
  font-size: 16px;
  display: block;
  position: relative;
}

.intro {
  width: 100%;
  font-size: 20px;
  line-height: 1.5;
}

#new-btn {
  height: 30px;
  color: #f5f5f5;
  float: right;
  background-color: #bbada0;
  border-style: none;
  border-radius: 10px;
  outline: 0;
  padding: 4px;
}

#new-btn:hover {
  background-color: #afa184;
  border-radius: 10px;
}

.game-board {
  width: 600px;
  height: 600px;
  background-color: #bbada0;
  border-radius: 10px;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.game-board div {
  width: 80px;
  height: 80px;
  text-align: center;
  color: #afa192;
  background-color: #eee4da;
  border-radius: 3px;
  margin: 6px;
  font-weight: bold;
  line-height: 80px;
}

[data-digits="0"] {
  color: #afa184 !important;
}

[data-digits="1"] {
  font-size: 60px;
}

[data-digits="2"] {
  font-size: 48px;
}

[data-digits="3"] {
  font-size: 36px;
}

[data-digits="4"] {
  font-size: 32px;
}

@media only screen and (max-width: 700px) {
  .container {
    width: 100%;
  }

  .intro {
    width: 95%;
    flex-direction: column;
    align-items: center;
    row-gap: 10px;
    display: flex;
  }

  .title {
    width: 95%;
    flex-direction: column;
    align-items: center;
    row-gap: 10px;
  }

  .game-board {
    width: 444px;
